          <label></label>->
          <label></label>->
          <label></label>->
          <label></label>->
          <label>offer</label>->
          <label></label>->
          <el-select v-model="talent.status" style="width: 130px" size="mini" placeholder="" @change="updateStatus">
            <el-option
              v-for="item in status.finish"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </div>

        <div v-if="talent.status > 14">
          <label></label>->
          <label></label>->
          <label></label>->
          <label></label>->
          <label></label>->
          <label>offer</label>->
          <label></label>->
          <label v-if="talent.status == 15"></label>
          <label v-if="talent.status == 16"></label>
        </div>

        <div style="margin-top: 20px;" v-if="talent.status == 11 || talent.status > 12">
          <el-input prefix-icon="el-icon-edit" v-model="talent.level" size="mini" style="width: 150px"
                    placeholder=""></el-input>
          <el-input prefix-icon="el-icon-edit" v-model="talent.profits" size="mini" style="width: 150px"
                    placeholder=""></el-input>
          <el-date-picker
            v-model="talent.entryDate"
            type="date"
            style="width: 150px"
            class="el-input--mini"
            default-time="09:00:00"
            value-format="yyyy-MM-dd"
            placeholder="">
          </el-date-picker>
          <el-input prefix-icon="el-icon-edit" v-model="talent.entryMark" size="mini" style="width: 150px"
                    placeholder=""></el-input>
          <el-button size="mini" type="primary" @click="updateEntryInfo()"> </el-button>
        </div>

      <span slot="footer" class="dialog-footer">
             <el-button size="mini" @click="cancelStatus"> </el-button>
        <!-- <el-button size="mini" type="primary" @click="updateStatus()"> </el-button>-->
          </span>

      </template>


    </el-dialog>

  </div>
</template>

<script>
  export default {
    data() {
      return {
        radio: '1',
        talents: [],
        name: '',
        hrName: '',
        fileUploadBtnText: '',
        beginDateScope: '',
        faangledoubleup: 'fa-angle-double-up',
        faangledoubledown: 'fa-angle-double-down',
        dialogTitle: '',
        statusDialogTitle: '',
        depTextColor: '#c0c4cc',
        multipleSelection: [],
        hrList: [],
        totalCount: -1,
        currentPage: 1,
        pageSize : 10,
        oldStatus:null,
        level:null,  //
        profits:null, //
        entryDate:null , //
        entryMark:null, //
        //appointmentList: [{id: 1, name: ''}, {id: 2, name: ''}],
        status: {
          appoint: [{id: 0, name: ''}, {id: 1, name: ''}, {id: 2, name: ''}],
          isInside:[{id: 1, name: ''}, {id: 3, name: ''}, {id: 4, name: ''}],
          inside: [{id: 3, name: ''}, {id: 5, name: ''}, {id: 6, name: ''}],
          isCustomer:[{id: 5, name: ''}, {id: 7, name: ''}, {id: 8, name: ''}],
          customer: [{id: 7, name: ''}, {id: 9, name: ''}, {id: 10, name: ''}],
          offer: [{id: 9, name: 'offer'}, {id: 11, name: 'offer'}, {id: 12, name: 'offer'}],
          entry: [{id: 11, name: ''}, {id: 13, name: ''}, {id: 14, name: ''}],
          finish: [{id: 13, name: ''}, {id: 15, name: ''}, {id: 16, name: ''}]
        },
        allStatus:[{id: 1, name: ''}, {id: 2, name: ''},{id: 3, name: ''}, {id: 4, name: ''},{id: 5, name: ''}, {id: 6, name: ''},
          {id: 7, name: ''}, {id: 8, name: ''}, {id: 9, name: ''}, {id: 10, name: ''}, {id: 11, name: 'offer'}, {id: 12, name: 'offer'} ,
          {id: 13, name: ''}, {id: 14, name: ''}, {id: 15, name: ''}, {id: 16, name: ''}],
        dialogVisible: false,
        tableLoading: false,
        advanceSearchViewVisible: false,
        showOrHidePop: false,
        showOrHidePop2: false,
        statusDialogVisible: false,
        talent: {
          id: null,
          hrId: null,
          name: null,
          phone: null,
          technology: null,
          workCity: null,
          lastCompany: null,
          school: null,
          graduation: null,
          major: null,
          education: null,
          lastSalary: null,
          hopeSalary: null,
          communicate: null,
          interviewDate: null,
          recommend: null,
          insideResult: null,
          customerResult: null,
          hrName: null,
          remark: null,
          channel: null,
          status: null,
          level : null,
          profits : null,
          entryDate : null,
          entryMark : null
        },
        rules: {
          name: [{required: true, message: ':', trigger: 'blur'}],
          phone: [{required: true, message: ':', trigger: 'blur'}]
        }, pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          },
          shortcuts: [{
            text: '',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        countStart:null,
        countEnd:null
      };
    },
    mounted: function () {
      this.initData();
      this.loadTalent();
    },
    methods: {
      loadTalent: function () {
        var _this = this;
        this.tableLoading = true;

        var param = "?pageNo=" + this.currentPage + "&pageSize=" + this.pageSize + "&name=" + _this.name;
        if (this.talent.hrId != null) {
          param += "&hrId=" + this.talent.hrId;
        }
        if (this.talent.status != null && this.talent.status != 0) {
          param += "&status=" + this.talent.status;
        }
        if (this.talent.phone != null) {
          param += "&phone=" + this.talent.phone;
        }
        if (this.talent.technology != null) {
          param += "&technology=" + this.talent.technology;
        }

        this.getRequest("/talent/basic/getTalentPage" + param).then(resp => {
          this.tableLoading = false;
          if (resp && resp.status == 200) {
            var data = resp.data;
            _this.talents = data.data.talents;
            _this.totalCount = data.data.count;
          }
        })
      }, currentChange(currentChange) {
        this.currentPage = currentChange;
        this.loadTalent();
      }, searchTalent() {
        this.loadTalent();
      }, nameChange() {
        this.loadTalent();
      }, updateStatus() {
        this.postRequest("/talent/basic/updateTalent", {"id": this.talent.id, "status": this.talent.status}).then(resp => {
          if (resp && resp.status == 200) {
            var data = resp.data;
            if (data.resultCode == 1) {
              this.$message({type: resp.status, message: "!"});
            }else  if (data.resultCode == 10001) {
              this.$message({type: resp.status, message: "!"});
              this.talent.status = this.oldStatus;
              this.statusDialogVisible = false;
            }
          }
        });
      }, showAdvanceSearchView() {
        this.emptyTalentData();
        this.advanceSearchViewVisible = !this.advanceSearchViewVisible;
        this.name = '';
        if (!this.advanceSearchViewVisible) {
          this.beginDateScope = '';
          this.loadTalent();
        }
      },
      emptyTalentData() {
        this.talent = {
          id: null,
          hrId: null,
          name: null,
          phone: null,
          technology: null,
          workCity: null,
          lastCompany: null,
          school: null,
          graduation: null,
          major: null,
          education: null,
          lastSalary: null,
          hopeSalary: null,
          communicate: null,
          interviewDate: null,
          recommend: null,
          insideResult: null,
          customerResult: null,
          hrName: null,
          remark: null,
          channel: null,
          status: null,
          level : null,
          profits : null,
          entryDate : null,
          entryMark : null

        }
      },
      cancelSearch() {
        this.advanceSearchViewVisible = false;
        this.emptyTalentData();
        this.beginDateScope = '';
        this.loadTalent();
      }, initData() {
        var _this = this;
        this.getRequest("/talent/basic/basicData").then(resp => {
          if (resp && resp.status == 200) {
            var data = resp.data;
            _this.hrList = data.data.hrList;
          }
        })
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      fileUploadSuccess(response, file, fileList) {
        if (response.resultCode == 1) {
          this.$message({type: response.status, message: ""});
        }
        this.loadTalent();
        this.fileUploadBtnText = '';
      },
      fileUploadError(err, file, fileList) {
        this.$message({type: 'error', message: "!"});
        this.fileUploadBtnText = '';
      },
      beforeFileUpload(file) {
        this.fileUploadBtnText = '';
      },
      showEditTalentView(row) {
        this.dialogTitle = "";
        this.talent = row;
        this.dialogVisible = true;
      }, updateTalent(formName) {
        this.tableLoading = true;
        var _this = this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            var obj = new Object();

            for(var o in this.talent){
              if(this.talent[o]){
                obj[o] = this.talent[o];
              }
            }
            if (this.talent.id) {
              //
              this.postRequest("/talent/basic/updateTalent", obj).then(resp => {
                _this.tableLoading = false;
                if (resp && resp.status == 200) {
                  var data = resp.data;
                  if (data.resultCode == 1) {
                    this.$message({type: resp.status, message: "!"});
                  }else  if (data.resultCode == 10001) {
                    this.$message({type: resp.status, message: "!"});
                  }
                  _this.dialogVisible = false;
                  //_this.emptyTalentData();
                  //_this.loadTalent();
                }
              })
            } else {
              //
              this.postRequest("/talent/basic/add", obj).then(resp => {
                _this.tableLoading = false;
                if (resp && resp.status == 200) {
                  var data = resp.data;
                  if (data.resultCode == 1) {
                    this.$message({type: resp.status, message: "!"});
                  }
                  _this.dialogVisible = false;
                  //_this.emptyTalentData();
                  //_this.loadTalent();
                }
              })
            }
          }
        });
        _this.emptyTalentData();
        _this.loadTalent();
      }, exportTalent() {
        var _this = this;
        var url = "/talent/basic/exportTalent";
        var param = "?name=" + _this.name;
        if (this.talent.hrId != null) {
          param += "&hrId=" + this.talent.hrId;
        }
       if (this.talent.status != null) {
          param += "&status=" + this.talent.status;
        }
        if (this.talent.phone != null) {
          param += "&phone=" + this.talent.phone;
        }
        if (this.talent.technology != null) {
          param += "&technology=" + this.talent.technology;
        }
        window.open(url + param, "_parent");
      }, showStatusDialog(row) {
        this.statusDialogTitle = "";
        this.statusDialogVisible = true;
        this.talent = row;
        this.oldStatus = this.talent.status;
      }, cancelEdit() {
        this.dialogVisible = false;
        this.emptyTalentData();
      }, cancelStatus() {
        this.statusDialogVisible = false;
        this.emptyTalentData();
      }, showAddTalentView() {
        this.emptyTalentData();
        this.dialogTitle = "";
        this.dialogVisible = true;
      },exportCount() {
        if(this.countStart && this.countEnd){
          var url = "/talent/basic/exportCount?countStart=" + new Date(this.countStart).valueOf() + "&countEnd=" + new Date(this.countEnd).valueOf();
          window.open(url, "_parent");
        }else{
          this.$message({message: "!"});
        }
      },updateEntryInfo(){
        var _this = this;
        var obj = new Object();
        obj['id'] = this.talent.id;
        if(this.talent.level){
          obj['level'] = this.talent.level;
        }
        if(this.talent.profits){
          obj['profits'] = this.talent.profits;
        }
        if(this.talent.entryDate){
          obj['entryDate'] = this.talent.entryDate;
        }
        if(this.talent.entryMark){
          obj['entryMark'] = this.talent.entryMark;
        }

        this.postRequest("/talent/basic/updateTalent", obj).then(resp => {
          _this.tableLoading = false;
          if (resp && resp.status == 200) {
            var data = resp.data;
            if (data.resultCode == 1) {
              this.$message({type: resp.status, message: "!"});
            }else  if (data.resultCode == 10001) {
              this.$message({type: resp.status, message: "!"});
            }
          }
        })
      },dialogClose(done){
        this.emptyTalentData();
        done();
      }
    }
  }
</script>

<style>
  .el-table .cell {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    white-space: nowrap!important;
    word-break: break-all;
    line-height: 23px;
    text-overflow: ellipsis;
  }
  .el-table .cell div {
    display: block;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }

  .is-leaf{
    font-family: STHeiti Light;
    background-color: #abdae4!important;
  }
  .el-table td, .el-table th.is-leaf {
    border-bottom: 1px solid gray;
  }
  .el-table--border td, .el-table--border th, .el-table__body-wrapper .el-table--border.is-scrolling-left~.el-table__fixed {
    border-right: 1px solid gray;
  }
</style>



